class Api {
  constructor(config) {
    this._baseUrl = config.baseUrl;
    this._headers = config.headers;
    this._cardsUrl = `${this._baseUrl}/cards`;
    this._userUrl = `${this._baseUrl}/users/me`;
    this._registerUrl = `${this._baseUrl}/signup`;
    this._loginUrl = `${this._baseUrl}/signin`;
    this._logoutUrl = `${this._baseUrl}/logout`;
  }

  _handleResponse(response) {
    if (response.ok) {
      return response.json();
    } else {
      return response.json().then((data) => Promise.reject(new Error(data.message)))
    }
  }

  registerUser(loginData) {
    return fetch(this._registerUrl, {
      method: "POST",
      headers: this._headers,
      body: JSON.stringify(loginData),
    }).then((res) => this._handleResponse(res));
  }

  loginUser(loginData) {
    return fetch(this._loginUrl, {
      method: "POST",
      headers: this._headers,
      body: JSON.stringify(loginData),
    }).then((res) => this._handleResponse(res));
  }

  logoutUser() {
    delete this._headers.Authorization;
  }

  loadLoginedUser(jwt) {
    this._headers.Authorization = `Bearer ${jwt}`;
    return fetch(this._userUrl, {
      headers: this._headers,
    }).then((res) => this._handleResponse(res))
  }

  getInitialCards() {
    return fetch(this._cardsUrl, {
      headers: this._headers,
    }).then((response) => this._handleResponse(response));
  }

  getUserInfo() {
    return fetch(this._userUrl, {
      headers: this._headers,
    }).then((response) => this._handleResponse(response));
  }

  patchUserInfo(info) {
    return fetch(this._userUrl, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify({ name: info.name, about: info.about }),
    }).then((response) => this._handleResponse(response));
  }

  patchUserAvatar(link) {
    const avaUrl = `${this._userUrl}/avatar`;
    return fetch(avaUrl, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify({ avatar: link }),
    }).then((response) => this._handleResponse(response));
  }

  handleLike(cardId, isLiked) {
    const method = isLiked ? "DELETE" : "PUT";
    return fetch(`${this._cardsUrl}/${cardId}/likes`, {
      method: method,
      headers: this._headers,
    }).then((response) => this._handleResponse(response));
  }

  addCard(cardData) {
    return fetch(this._cardsUrl, {
      method: "POST",
      headers: this._headers,
      body: JSON.stringify(cardData),
    }).then((response) => this._handleResponse(response));
  }

  deleteCard(cardId) {
    return fetch(`${this._cardsUrl}/${cardId}`, {
      method: "DELETE",
      headers: this._headers,
    }).then((response) => this._handleResponse(response));
  }
}

const api = new Api({
  baseUrl: "https://api.mesto.mahakomar.nomoredomains.club",
  headers: {
    "Content-type": "application/json",
  },
});

export default api;