import React from 'react';
import { Route, Link, useHistory, Switch } from 'react-router-dom';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import PopupWithForm from './PopupWithForm';
import ImagePopup from './ImagePopup';
import api from '../utils/api';
import CurrentUserContext from '../contexts/CurrentUserContext';
import spinnerPath from '../images/spinner.gif';
import EditProfilePopup from './EditProfilePopup';
import EditAvatarProfile from './EditAvatarProfile';
import AddPlacePopup from './AddPlacePopup';
import Login from './Login';
import Register from './Register';
import InfoTooltip from './InfoTooltip';
import ProtectedRoute from './ProtectedRoute';
import { badInfoTooltipMessage } from '../utils/constants';

function App() {
  // Set states
  const history = useHistory();
  const [loggedIn, setLoggedIn] = React.useState(false);
  const [email, setEmail] = React.useState('');
  const [isEditProfilePopupOpen, setEditProfilePopupOpen] = React.useState(
    false
  );
  const [isEditAvatarPopupOpen, setEditAvatarPopupOpen] = React.useState(false);
  const [isAddPlacePopupOpen, setAddPlacePopupOpen] = React.useState(false);
  const [isInfoTooltipOpen, setInfoTooltipOpen] = React.useState(false);
  const [isInfoTooltipOk, setInfoTooltipOk] = React.useState(true);
  const [infoTooltipMessage, setInfoTooltipMessage] = React.useState(
    badInfoTooltipMessage
  );
  const [currentUser, setCurrentUser] = React.useState({
    id: '',
    name: '...',
    about: '...',
    avatar: spinnerPath,
  });
  const [cards, setCards] = React.useState([]);
  const [selectedCard, setSelectedCard] = React.useState({
    name: '',
    link: '',
  });

  // Check, if user is logged in
  React.useEffect(() => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      api
        .loadLoginedUser(jwt)
        .then((data) => {
          if (data) {
            setEmail(data.email);
            setLoggedIn(true);
            history.push('/');
          }
        })
        .catch((err) => console.log(err.message));
    }
  }, [history, loggedIn]);

  // Get user data
  React.useEffect(() => {
    if (loggedIn)
      Promise.all([api.getUserInfo(), api.getInitialCards()])
        .then(([userData, cardsData]) => {
          setCurrentUser({
            id: userData._id,
            name: userData.name,
            about: userData.about,
            avatar: userData.avatar,
          });
          setCards(cardsData.cards);
          setEmail(userData.email)
        })
        .catch((err) => console.log(err.message));
  }, [loggedIn]);

  // Popups handlers
  function handleEditProfileClick() {
    setEditProfilePopupOpen(true);
  }

  function handleEditAvatarClick() {
    setEditAvatarPopupOpen(true);
  }

  function handleAddPlaceClick() {
    setAddPlacePopupOpen(true);
  }

  function closeAllPopups() {
    setEditProfilePopupOpen(false);
    setEditAvatarPopupOpen(false);
    setAddPlacePopupOpen(false);
    setInfoTooltipOpen(false);
    setSelectedCard({ name: '', link: '' });
  }

  // Card handlers
  function handleCardClick(card) {
    setSelectedCard(card);
  }

  function handleCardLike(card) {
    const isLiked = card.likes.some((user) => user === currentUser.id);
    api
      .handleLike(card._id, isLiked)
      .then((newCard) => {
        const newCards = cards.map((c) => (c._id === card._id ? newCard : c));
        setCards(newCards);
      })
      .catch((err) => console.log(err.message));
  }

  function handleCardDelete(card) {
    api
      .deleteCard(card._id)
      .then(() => {
        const newCards = cards.filter((c) => c._id !== card._id);
        setCards(newCards);
      })
      .catch((err) => console.log(err.message));
  }

  // Add card handler
  function handleAddPlaceSubmit(cardData) {
    api
      .addCard(cardData)
      .then((newCard) => {
        setCards([newCard, ...cards]);
        closeAllPopups();
      })
      .catch((err) => console.log(err.message));
  }

  // Update user's data handlers
  function handleUpdateUser(newInfo) {
    api
      .patchUserInfo(newInfo)
      .then(() => {
        setCurrentUser({
          ...currentUser,
          name: newInfo.name,
          about: newInfo.about,
        });
        closeAllPopups();
      })
      .catch((err) => console.log(err.message));
  }

  function handleUpdateAvatar(newAvatar) {
    api
      .patchUserAvatar(newAvatar.avatar)
      .then(() => {
        setCurrentUser({
          ...currentUser,
          avatar: newAvatar.avatar,
        });
        closeAllPopups();
      })
      .catch((err) => console.log(err.message));
  }

  // Autorization handlers
  function handleRegister(loginData) {
    api
      .registerUser(loginData)
      .then((data) => {
        setInfoTooltipOk(true);
        history.push('/sign-in');
      })
      .catch((err) => {
        setInfoTooltipMessage(err.message);
        setInfoTooltipOk(false);
      })
      .finally(() => setInfoTooltipOpen(true));
  }

  function handleLogin(loginData) {
    api
      .loginUser(loginData)
      .then((data) => {
        setLoggedIn(true);
        localStorage.setItem('jwt', data.token);
        history.push('/');
      })
      .catch((err) => {
        setInfoTooltipMessage(err.message);
        setInfoTooltipOk(false);
        setInfoTooltipOpen(true);
      });
  }

  function handleLogout() {
    localStorage.removeItem('jwt');
    setLoggedIn(false);
    api.logoutUser();
  }

  return (
    <CurrentUserContext.Provider value={currentUser}>
      <div className="page">
        <Switch>
          <Route exact path="/sign-up">
            <Header>
              <Link to="/sign-in" className="header__link">
                Войти
              </Link>
            </Header>
            <Register onRegister={handleRegister} />
          </Route>

          <Route exact path="/sign-in">
            <Header>
              <Link to="/sign-up" className="header__link">
                Регистрация
              </Link>
            </Header>
            <Login onLogin={handleLogin} />
          </Route>
          <ProtectedRoute exact path="/" loggedIn={loggedIn}>
            <Header>
              <p className="header__login-info">{email}</p>
              <Link
                to="/sign-in"
                className="header__link header__link_shy"
                onClick={handleLogout}
              >
                Выйти
              </Link>
            </Header>
            <Main
              cards={cards}
              onEditProfile={handleEditProfileClick}
              onEditAvatar={handleEditAvatarClick}
              onAddPlace={handleAddPlaceClick}
              onCardClick={handleCardClick}
              onCardLike={handleCardLike}
              onCardDelete={handleCardDelete}
            />
            <Footer />
          </ProtectedRoute>
        </Switch>

        <EditProfilePopup
          isOpen={isEditProfilePopupOpen}
          onClose={closeAllPopups}
          onUpdateUser={handleUpdateUser}
        />
        <EditAvatarProfile
          isOpen={isEditAvatarPopupOpen}
          onClose={closeAllPopups}
          onUpdateAvatar={handleUpdateAvatar}
        />
        <AddPlacePopup
          isOpen={isAddPlacePopupOpen}
          onClose={closeAllPopups}
          onAddPlace={handleAddPlaceSubmit}
        />
        <PopupWithForm
          name="delete-card"
          title="Вы уверены?"
          buttonText="Да"
          onClose={closeAllPopups}
        />
        <ImagePopup card={selectedCard} onClose={closeAllPopups}></ImagePopup>
        <InfoTooltip
          ok={isInfoTooltipOk}
          isOpen={isInfoTooltipOpen}
          onClose={closeAllPopups}
          message={infoTooltipMessage}
        />
      </div>
    </CurrentUserContext.Provider>
  );
}

export default App;

